<template>
    <v-container fluid>
        <v-row justify="space-between" class="my-3"> 
            <v-col cols="auto">
                <h2 class=" font-weight-bold">Annealing Process</h2>
            </v-col>
            
            <v-col cols="auto" class="pb-0">
                <v-row justify="end">
                    <v-col cols="auto">
                        <v-menu
                            ref="menu"
                            v-model="addedFromMenu"
                            :close-on-content-click="false"
                            transition="scale-transition"
                            offset-y
                            min-width="290px"
                        >
                            <template v-slot:activator="{ on, attrs }">
                                <v-text-field
                                    dense
                                    outlined
                                    color="grey"
                                    v-model="addedFrom"
                                    placeholder="Select Date"
                                    readonly
                                    v-bind="attrs"
                                    v-on="on"
                                    clearable
                                    @click:clear="clearSearch('date')"
                                    class="body-1"
                                ></v-text-field>
                            </template>
                            <v-date-picker
                                v-model="addedFrom"
                                no-title
                                scrollable
                                @input="addedFromMenu = false"
                                @change="setOptions"
                                >
                            </v-date-picker>
                        </v-menu>
                    </v-col>
                    <v-col cols="auto">
                        <v-btn
                        dark
                        class="py-4 body-2 font-weight-bold"
                        @click="openReport()"
                        >View Report</v-btn
                        >
                    </v-col>
                </v-row>
            </v-col>
        </v-row>
        <v-data-table 
            v-model="$store.state.selRows"
            :headers="headers"
            :items="$store.state.annealing.batches"
            item-key="ID"
            class="elevation-1 coils"
            :loading="$store.state.isLoading"
            disable-pagination
            fixed-header
            height="calc(100vh - 190px)"
            hide-default-footer
        >
            <template v-slot:[`item.date`]="{item}">
                <div class="body-2"> 
                    <span>{{ item.date ? $options.filters.formatDate(item.date) : '---'}}</span>
                </div>
            </template>
            <template v-slot:[`item.status`]="{item}">
                <div class="body-2"> 
                    <span :class="getTextColor(item.status)" class="text-capitalize">{{ item.status }}</span>
                </div>
            </template>
            <template v-slot:[`item.shift`]="{item}">
                <div class="body-2"> 
                    <span class="text-capitalize">{{ getShiftName(item.shift) }}</span>
                </div>
            </template>
            <template v-slot:[`item.base`]="{item}">
                <div class="body-2"> 
                    <span class="text-capitalize">Base {{ item.base }}</span>
                </div>
            </template>
            <template v-slot:[`item.furnace`]="{item}">
                <div class="body-2"> 
                    <span class="text-capitalize">Furnace {{ item.furnace }}</span>
                </div>
            </template>
            <template v-slot:[`item.temperature`]="{item}">
                <div class="body-2"> 
                    <span class="text-capitalize">{{ item.temperature ?  `${item.temperature}(°C)`  : "---"}}</span>
                </div>
            </template>
            <template v-slot:[`item.actions`]="{item}">
                <v-row align="center">
                    <!-- :class="checkRole('member')? 'pb-0' : ''" -->
                    <v-col v-if="item.status === 'in-queue for annealing'" @click="processBatch(item, 'in-queue for annealing')" cols="12" class="pb-0">
                        <v-btn small outlined class="caption">Edit Annealing Planning</v-btn>
                    </v-col>
                    <v-col v-if="item.status === 'in-queue for annealing'" @click="processBatch(item, 'in-process for annealing')" cols="12">
                        <v-btn small outlined class="caption">Start Processing</v-btn>
                    </v-col>
                    <v-col v-if="checkRole('member') && item.status === 'in-process for annealing'" @click="processBatch(item, 'approve for annealing')" cols="12">
                        <v-btn small outlined class="caption">Submit Report</v-btn>
                    </v-col>
                    <v-col v-if="checkRole('admin') && item.status === 'approve for annealing'" @click="processBatch(item, 'annealed')" cols="12">
                        <v-btn small outlined class="caption">View & Mark Complete</v-btn>
                    </v-col>
                </v-row>
        </template>
        </v-data-table>
        <v-row>
            <FurnaceReport v-if="$store.state.annealing.report"/>
            <AnnealingPlanning v-if="$store.state.annealing.drawer"/>
        </v-row>
    </v-container>
</template>

<script>
    import FurnaceReport from "@/components/FurnaceReport";
    import annealing from "@/services/annealing";
    import AnnealingPlanning from '@/components/drawers/AnnealingPlanning';
    export default {
        components: {
            AnnealingPlanning,
            FurnaceReport
        },
        data () {
            return {
                selDate: null,
                dateMenu: false,
                data: [],
                selThickness: '',
                selRows: [],
                singleSelect: false,
                options: {},
                sortBy: '',
                orderBy: 'desc',
                selStatus: null,
                addedFromMenu: false,
                addedFrom: null,
                headers: [
                { text: 'Charge No.', value: 'charge_no', sortable: false, width:"10%"},
                { text: 'Priority', value: 'priority', sortable: false, width:"10%" },
                { text: 'Shift', value: 'shift', sortable: false, width:"10%" },
                { text: 'Date', value: 'date', sortable: false,width:"10%" },
                { text: 'Base', value: 'base', sortable: false,width:"10%" },
                { text: 'Furnace', value: 'furnace', width:"10%" },
                {
                    text: 'Temperature',
                    align: 'start',
                    value: 'temperature',
                    sortable: false,
                    width:"10%"
                },
                { text: 'Status', value: 'status', sortable: false, width:"15%"},
                { text: 'Actions', value: 'actions', sortable: false,align: 'end', width:"10%" }],
       
            }
        },
        mounted() {
            this.$store.state.selRows = []
            this.$store.dispatch('annealing/getBatches', {status: 'in-queue for annealing,in-process for annealing,approve for annealing'})
            this.$store.dispatch('getShifts')
            // this.$store.dispatch('getThicknesses')
            this.$store.dispatch('getSlittedCoils', {status: 'rolled,rewinded,in-queue for annealing'});  
            
            this.$store.dispatch("furnaces/getFurnaceList");
            this.$store.dispatch("bases/getBaseList");
        },
        watch: {
            options: {
                handler (newVal) {
                const { sortBy, sortDesc } = newVal
                if (sortBy.length === 1 && sortDesc.length === 1) {
                if(sortDesc[0] == true) this.orderBy = 'desc'
                else this.orderBy = 'asc'
                this.sortBy = sortBy[0]
                }
                this.searchData()
                },
                deep: true,
            },
        },
        methods: {
            openReport() {
                this.$store.state.annealing.report = true
            },
            processBatch(item, status) {
                this.data.status = status
                this.$store.state.annealing.batchId = item.id
                this.$store.state.annealing.selItem = item
                if(status === "in-queue for annealing") this.$store.state.annealing.drawer = true
                if(status === "in-process for annealing" || status === "approve for annealing") this.processPlanning(status)
                if(status === "annealed") {
                    this.$store.state.annealing.drawer = true
                }

            },
            async processPlanning(status) {
                let data = {}
                let date = this.$options.filters.calendarDate(
                new Date().toISOString()
                );
                data.updated_at = date
                data.status = status
                try {
                this.$store.state.isLoading = true;
                const result = await annealing.update(this.$store.state.annealing.batchId, data);
                console.log("result", result);
                } catch (error) {
                console.log("error", error);
                } finally {
                this.$store.state.isLoading = false;
                this.$store.dispatch('annealing/getBatches', {status: 'in-queue for annealing,in-process for annealing,approve for annealing'})
                }
            
            },
            checkRole(role_name) {
                let user = JSON.parse(localStorage.getItem('user'))
                if(user && user.role == role_name) return true
                else return false 
            },
            getShiftName(shift) {
                if(shift === 1) return 'Day Shift'
                else if(shift === 2) return 'Night Shift'
                else return '24 Hour Shift'
            },
            editPlanning(item) {
                let date = this.$options.filters.calendarDate(item.rolling_date).split(" ");
                // state.slittedDate = date[0];
                item.rolling_date = date[0]
                item.dateMenu = false
                this.$store.state.rolling.selRows = [item];
                this.$store.state.rolling.dialog = true  
            },
            setOptions() {
                this.searchData()
            },
            getTextColor(type) {
                let color = ''
                switch(type) {
                    case 'slitted': 
                    color = 'grey--text text--darken-1'
                    break
                    case 'available': 
                    color = 'green--text text--darken-2'
                    break
                    case 'edit-required': 
                    color = 'red--text text--lighten-1'
                    break 
                    default :
                    color = 'yellow--text text--darken-4'
                    break
                }
                return color
            },
            clearSearch(type) {
               if(type === 'date') this.addedFrom = null
               else if(type === 'status') this.selStatus = null
               else if(type === 'company') this.selCompany = null
               else if(type === 'thickness') this.selThickness = null
               this.setOptions() 
            },
            searchData() {
                let payload = {status: 'in-queue for annealing,in-process for annealing,approve for annealing'}
                if(this.addedFrom) payload.date = this.addedFrom
                if(this.selThickness) payload.rolling_thickness = this.selThickness
                this.$store.dispatch('annealing/getBatches', payload)
            }
        }
    }
</script>


<style>
.v-data-table.coils>.v-data-table__wrapper>table>thead>tr>th {
  font-size: 16px !important;
  color: black !important;
}
.v-data-table.coils>.v-data-table__wrapper>table>tbody>tr>td {
  padding: 10px 15px ;
}
.customize-list .v-list-item {
  min-height: 33px !important;
}
.search-list {
        color: grey !important; 
        padding-top: 18px !important; 
        padding-bottom: 20px !important;
    }
    .select-box.v-text-field.v-text-field--enclosed{
        max-width: 200px;
    }
</style>